<template>
<div v-if="loaded">
        <v-row v-for="(row, x) in game.board.points" :key="x" class="d-flex">
            <input v-for="(point, y) in row" :key="y" type="button" :style="'width:3%;background-color:'+point.render()" @click="player.playStone(point)"/>

        </v-row>
        <v-btn @click="switchPlayer()" :color="player.stoneColor">
            Switch Player
        </v-btn>
        <v-btn v-if="player.activePlayer == true" @click="player.passTurn()">
            Pass Turn
        </v-btn>
</div>
</template>

<script>
import Stone from '../classes/Stone.ts'
import Game from '../classes/Game.ts'

export default {
    name: 'goboard',
    data() {
        return {
            game: undefined,
            player: undefined,
            loaded: false
        }
    },
    methods: {
        switchPlayer(){
            if(this.player == this.game.playerWhite) {
                this.player = this.game.playerBlack
            } else {
                this.player = this.game.playerWhite
            }
        },

        newStone(){
            return new Stone('black')
        }
    },
    computed: {

    },

    mounted() {
        this.game = new Game()
        this.player = this.game.playerBlack
        this.loaded = true
    }
}
</script>
